<template>
  <li
    class="bookrackItem"
    v-if="book"
    @click="
      $emit('to-read', {
        id: book.detail.id,
        book: book,
      })
    "
  >
    <van-swipe-cell>
      <van-card :thumb="`http://statics.zhuishushenqi.com${book.book.cover}`">
        <template #tags>
          <p class="van-card__content-title">{{ book.book.title }}</p>
          <p class="van-card__content-chapter">
            <template v-if="book.detail.chapter">
              <span>{{ book.detail.index + 1 }} 章</span>
              <i>/</i>
              <span>{{ book.detail.chapter.length }} 章</span>
            </template>
            <template v-else>
              <van-icon dot />
              <span>未读过</span>
            </template>
          </p>
        </template>
      </van-card>
      <!-- 滑动块 -->
      <template #right>
        <van-button
          square
          text="删除"
          type="danger"
          class="delete-button"
          @click="delBookrack(book.detail.id)"
        />
      </template>
    </van-swipe-cell>
  </li>
</template>

<script>
import { mapMutations } from "vuex";
export default {
  props: ["book"],
  methods: {
    ...mapMutations(['delBookrack'])
  },
};
</script>

<style lang="less">
.bookrackItem {
  .van-swipe-cell {
    .van-card {
      background: #fff;
      .van-card__header {
        height: 78px;
        .van-card__thumb {
          width: 58px;
          height: 78px;
        }
        .van-card__content {
          padding-top: 20px;
          padding-left: 10px;
          box-sizing: border-box;
          .van-card__content-title {
            font-size: 14px;
            margin-bottom: 5px;
          }
          .van-card__content-chapter {
            font-size: 12px;
            color: #999;
            span,
            i {
              vertical-align: middle;
              margin-left: 5px;
            }
          }
        }
      }
    }
    .van-swipe-cell__right {
      button {
        height: 94px;
      }
    }
  }
}
</style>