<script>
import { Toast } from "vant";
import { mapState } from "vuex";
import BookrackItem from "@/components/BookrackItem.vue";
import HeaderNav from "@/components/HeaderNav.vue";
import BgMask from "@/components/BgMask.vue";
export default {
  components: {
    BookrackItem,
    HeaderNav,
    BgMask,
  },
  data() {
    return {
      test: null,
      books: [],
      childObj: null, //接收子组件传上来的数据
      loading: false,
      timer: null,
    };
  },
  computed: mapState(["myBookRack"]),
  watch: {
    myBookRack() {
      this.searchBookRack();
    },
  },

  created() {
    document.documentElement.scrollTop = 0;
    this.searchBookRack();
  },
  // activated() {
  //   console.log("keep-alive");
  //   document.documentElement.scrollTop = 0;
  // },
  methods: {
    toRead(obj) {
      this.childObj = obj;
      console.log("sadajlkd", this.childObj);
      if (this.timer) {
        clearTimeout(this.timer);
      }
      this.timer = setTimeout(() => {
        Toast("正在前往，请稍后");
        this.$router.push({
          path: `/bookDetail/${obj.id}/read`,
          query: { bookName: obj.book.book.title },
        });
      }, 300);
    },
    searchBookRack() {
      let count = 0;
      this.books = [];
      this.loading = true;
      if (this.myBookRack.length == 0) {
        this.loading = false;
      }
      this.myBookRack.forEach((item, i) => {
        this.axios
          .get("/book-info/" + item.id)
          .then((res) => {
            this.books.push({ detail: this.myBookRack[i], book: res.data });
          })
          .finally(() => {
            count++;
            if (count == this.myBookRack.length) {
              this.loading = false;
            }
          });
      });
    },
  },

  beforeRouteLeave(to, from, next) {
    if (to.name === "Read") {
      // 如果当前的书籍的目录有效时间比当前时间小，或者当前书籍未读过是没有章节信息的，那么就需要更新目录
      if (
        this.childObj.book.detail.expire < Date.now() ||
        !this.childObj.book.detail.chapter
      ) {
        this.axios
          .get("/book-sources?view=summary&book=" + this.childObj.id)
          .then((res) => {
            this.axios.get("/book-chapters/" + res.data[0]._id).then((res) => {
              localStorage.setItem(
                "allChapters",
                JSON.stringify({
                  chapters: res.data.chapters,
                  index: this.childObj.book.detail.index,
                  // pageNum: this.childObj.book.detail.pageNum,
                })
              );
              this.$store.commit("getAllChapter", {
                chapters: res.data.chapters,
                index: this.childObj.book.detail.index,
                // pageNum: this.childObj.book.detail.pageNum,
              });
              console.log("书本需要更新");
              next();
            });
          });
      } else {
        console.log("书本还没过期");
        window.localStorage.setItem(
          "allChapters",
          JSON.stringify({
            chapters: this.childObj.book.detail.chapter,
            index: this.childObj.book.detail.index,
            // pageNum: this.childObj.book.detail.pageNum,
          })
        );
        this.$store.commit("getAllChapter", {
          chapters: this.childObj.book.detail.chapter,
          index: this.childObj.book.detail.index,
          // pageNum: this.childObj.book.detail.pageNum,
        });
        next();
      }
    } else {
      next();
    }
  },
};
</script>

<style lang="less">
.bookrack {
  padding: 0 10px;
  box-sizing: border-box;
  margin-bottom: 80px;
  .bookrack-contain {
    padding-top: 10px;
    border-radius: 10px;
    min-height: 60vh;
    background: #fff;
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.25);
  }
  .nullRack {
    text-align: center;
    p {
      color: #666;
    }
  }
}
</style>